<template>
    <div class="upLosePW">
          <van-nav-bar
            title="找回密码"
            left-text="返回"
            @click-left="back"
            left-arrow
        />
        <div class="box">
            <van-form @submit="onSubmit">
                    <van-field
                        type="text"
                        name="phone"
                        v-model="userinfo.phone"
                        label="手机号"
                        placeholder="请输入11位手机号"
                        maxlength='11'
                    >
                    </van-field>

                        <van-field
                            type='text'
                            label="新密码"
                            name="password"
                            v-model="userinfo.password"
                            placeholder="字母数字_组合(6-16位)"
                            maxlength="16"
                        >

                    </van-field>

                    <div class="sub_btn">
                        <van-button round block type="info" native-type="submit">
                           提交
                        </van-button>
                    </div>
            </van-form>
        </div>
    </div>
</template>

<script>
import validForm from '../assets/js/validForm'
    export default {
        data(){
            return {
                userinfo:{
                    phone: '',
                    password: ''
                }
            }
        },
        methods: {
            onSubmit(value){
                
                if (!validForm.validUserForm(value,this)) {
                    return;
                }
               
                this.$toast.loading({
                    message: '加载中...',
                    forbidClick: true,
                    duration: 0
                })
 
                this.axios({
                    method: "POST",
                    url: '/retrievePassword',
                    data:{
                       appkey: this.appkey,
                       phone: value.phone,
                       password:value.password

                    }
                }).then(result=>{
                    this.$toast.clear();
                    
                    this.$toast(result.data.msg);
                    if(result.data.code=="L001"){
                        this.$router.push({name: 'Login'});
                    }
                }).catch(err=>{
                    this.$toast.clear();
                    
                })
            },
            back(){
                this.$router.go(-1);
            }
        }
    }
</script>

<style lang="less" scoped>
   .box{
       width: calc(~"100% - 50px");
       padding: 25px;
   }
</style>